import { useState } from "react";
import { Container, Row, Col } from "react-bootstrap";
import contactImg from "../assets/img/contact-img.svg";
import 'react-animated-css';
import TrackVisibility from 'react-on-screen';

export const Contact2 = () => {
    return(
    <section className="contact section" id="connect">
        <Container>
            <Row className="align-items-center">
            <Col size={12} md={6}>
                <TrackVisibility>
                {({ isVisible }) =>
                    <img className={isVisible ? "animate__animated animate__zoomIn" : ""} src={contactImg} alt="Contact Us"/>
                }
                </TrackVisibility>
            </Col>
        <Col size={12} md={6}>
            <TrackVisibility> 
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                <span className="section__subtitle">Grab a coffe</span>
                <h2 className="section__title">Lets Talk</h2>
            
            <Row>    

            <div className="contact__container container grid">
                <div className="contact__content">
                    <h3 className="contact__title">Learn more</h3>

                    <div className="contact__info">
                        <div className="contact__card">
                            <Col>
                                <i class='bx bx-mail-send contact__card-icon'></i>
                                <h3 className="contact__card-title">Email</h3>
                                <span className="contact__card-data">Hassan.ea1999@hotmail.com</span>

                                <a href="mailto:hassan.ea1999@hotmail.com" target={"_blank"} className="contact__button">
                                    Write to me <i class='bx bx-right-arrow-alt contact__button-icon'></i>
                                </a>
                            </Col>
                        </div>

                        <div className="contact__card">
                            <Col>
                                <i class='bx bxl-github contact__card-icon'></i>
                                <h3 className="contact__card-title">Github</h3>
                                <span className="contact__card-data">Hassan Github</span>

                                <a href="https://github.com/SkyRider99" target={"_blank"} className="contact__button">
                                    To the Adventure <i class='bx bx-right-arrow-alt contact__button-icon'></i>
                                </a>
                            </Col>
                        </div>

                        <div className="contact__card">
                            <Col>
                                <i class='bx bxl-facebook contact__card-icon'></i>
                                <h3 className="contact__card-title">Discord</h3>
                                <span className="contact__card-data">Kitsune#8402</span>

                                <a href="https://www.discord.gg" target={"_blank"} className="contact__button">
                                    Learn more <i class='bx bx-right-arrow-alt contact__button-icon'></i>
                                </a>
                            </Col>
                        </div>
                    </div>
                </div>
            </div>
            </Row>
            </div>}
            </TrackVisibility>
            </Col>
        </Row>
      </Container>
    </section>
    )
}