import { Container, Row, Col } from "react-bootstrap";
import logo from "../assets/img/logo2.png";
import navIcon6 from "../assets/img/nav-icon6.png";
import navIcon4 from "../assets/img/nav-icon4.png";
import navIcon5 from "../assets/img/nav-icon5.png";

export const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          <Col size={12} sm={6}>
            <img src={logo} alt="Logo" />
          </Col>
          <Col size={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href="#"><img src={navIcon6} alt="Icon" /></a>
              <a href="https://www.discord.gg"><img src={navIcon4} alt="Icon" /></a>
              <a href="https://github.com/SkyRider99"><img src={navIcon5} alt="Icon" /></a>
            </div>
            <p>Copyright 2022. All Rights Reserved</p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}