import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/first.PNG";
import projImg2 from "../assets/img/bootstrap.PNG";
import projImg3 from "../assets/img/app-react.PNG";
import projImg4 from "../assets/img/app-vue.PNG";
import projImg5 from "../assets/img/app.PNG";
import projImg6 from "../assets/img/portfolio.png";
import projImg7 from "../assets/img/wordpress.PNG";
import projImg8 from "../assets/img/weather-app.PNG";
import projImg9 from "../assets/img/space-trvl.PNG";
import colorSharp2 from "../assets/img/color-sharp2.png";
import 'animate.css';
import TrackVisibility from 'react-on-screen';

export const Projects = () => {

  const projects = [
    {
      title: "First Website",
      description: "First year of school", 
      button: "https://skyrider99.github.io/firstwebsite/",
      imgUrl: projImg1,
    },
    {
      title: "Bootstrap Website",
      description: "At end of 1 year",
      button: "https://skyrider99.github.io/bootstrap/",
      imgUrl: projImg2,
    },
    {
      title: "React App",
      description: "2 year of school",
      button: "https://skyrider99.github.io/reactapp/",
      imgUrl: projImg3,
    },
    {
      title: "Vue App",
      description: "2 year of school",
      button: "https://skyrider99.github.io/vueapp/",
      imgUrl: projImg4,
    },
    {
      title: "First Mobile App",
      description: "2 year of school",
      button: "https://github.com/SkyRider99/SkyRider99.github.io/tree/main/mobilapp",
      imgUrl: projImg5,
    },
    {
      title: "First Design",
      description: "First prototype design for portfolio",
      button: "https://github.com/SkyRider99/SkyRider99.github.io/tree/main/firstdesign",
      imgUrl: projImg6,
    },
  ];

  const projects2 = [
    {
      title: "WordPress Site",
      description: "2 year of school Project",
      button: "https://github.com/SkyRider99/SkyRider99.github.io/tree/main/wordpress",
      imgUrl: projImg7,
    },
  ];

  const projects3 = [
    {
      title: "Weather App",
      description: "Weather app that shows weather of any city in the world",
      button: "https://skyrider99.github.io/weather2/",
      imgUrl: projImg8,
    },
    {
      title: "Space Travel Agency",
      description: "Website for a space agency that sells space vacation, concept!",
      button: "https://github.com/SkyRider99/SkyRider99.github.io/tree/main/space",
      imgUrl: projImg9,
    },
  ];

  return (
    <section className="project" id="project">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) =>
              <div className={isVisible ? "animate__animated animate__fadeIn": ""}>
                <h2>Projects</h2>
                <p>Here you can find a list of my projects that i did in school and in my free time. You can also find here projects/concept work i did after i graduated College.</p>
                <Tab.Container id="projects-tabs" defaultActiveKey="first">
                  <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                    <Nav.Item>
                      <Nav.Link eventKey="first">School</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="second">Wordpress</Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                      <Nav.Link eventKey="third">After</Nav.Link>
                    </Nav.Item>
                  </Nav>
                  <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                    <Tab.Pane eventKey="first">
                      <Row>
                        {
                          projects.map((project, index) => {
                            return (
                              <ProjectCard
                                key={index}
                                {...project}
                                />
                            )
                          })
                        }
                      </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="second">
                      <Row>
                          {
                            projects2.map((project, index) => {
                              return (
                                <ProjectCard
                                  key={index}
                                  {...project}
                                  />
                              )
                            })
                          }
                        </Row>
                    </Tab.Pane>
                    <Tab.Pane eventKey="third">
                        <Row>
                          {
                            projects3.map((project, index) => {
                              return (
                                <ProjectCard
                                  key={index}
                                  {...project}
                                  />
                              )
                            })
                          }
                        </Row>
                    </Tab.Pane>
                  </Tab.Content>
                </Tab.Container>
              </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2}></img>
    </section>
  )
}